import {Component} from '@angular/core';
import { Routes,Route,ROUTER_DIRECTIVES } from '@angular/router';

import {MdButton, MdAnchor} from '../components/button/button';
import {MdIcon} from '../components/icon/icon';

import { HeroPortalComponent } from './hero-portal/hero-portal.component';
import { TodoPortalComponent } from './todo-portal/todo-portal.component';
import { DownupAnimationComponent } from './xxj-animation/downup-animation.component';

export class Element {
    constructor(private nodeName: string, private parent?: Element) {
        
    }
    toString() {
        return '<' + this.nodeName + '>';
    }
};

@Component({
    selector:'my-app',
    template:`
             <h1>{{title}}</h1>
             <hr>
             <nav>
             <a [routerLink]="['hero-portal']" md-fab color="primary">Hero-Portal</a> 
             <a [routerLink]="['todo-portal']" md-raised-button color="accent">Todo-Portal</a> 
             <a [routerLink]="['xxj-animation']" md-raised-button color="accent">DownupAnimation</a>
             </nav>
             <router-outlet></router-outlet>
    `,
    styleUrls:["app/app.component.css"],
    directives:[ROUTER_DIRECTIVES,MdButton, MdAnchor, MdIcon],
    providers:[]  
})
@Routes([
new Route({ path:'/hero-portal', component:HeroPortalComponent}),
new Route({ path:'/todo-portal', component:TodoPortalComponent}),
new Route({ path:'/xxj-animation', component:DownupAnimationComponent})
])

export class AppComponent {
    title='This is Home';
}

